body {
  margin: 0;
  padding: 0;
  background: radial-gradient(circle at top, #f0f0f0, #c7c7c7);
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
  width: 100%;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  padding: clamp(8px, 3vw, 16px);
}

.game-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: clamp(6px, 2.5vw, 10px);
  margin-bottom: clamp(12px, 3vw, 20px);
  flex-wrap: wrap;
}

.dice-logo {
  width: clamp(28px, 8vw, 40px);
  height: clamp(28px, 8vw, 40px);
}

.game-title {
  font-weight: 700;
  font-size: clamp(1.2rem, 6vw, 2rem);
  background: linear-gradient(90deg, #ff0055, #0078ff);
  -webkit-background-clip: text;
  color: transparent;
  text-shadow: 1px 1px 2px #aaa;
}

.ludo-board {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: clamp(6px, 2vw, 10px);
  width: 100%;
  max-width: 100%;
  overflow: auto;
}

.cell-grid {
  position: relative;
  display: grid;
  grid-template-columns: repeat(15, clamp(16px, 4.5vw, 30px));
  grid-template-rows: repeat(15, clamp(16px, 4.5vw, 30px));
  background: #fff;
  border: clamp(2px, 1vw, 4px) solid #222;
  border-radius: clamp(6px, 1.5vw, 12px);
  box-shadow: 0 clamp(3px, 1vw, 6px) clamp(10px, 3vw, 20px) rgba(0, 0, 0, 0.25);
  gap: 0;
}

.zone {
  position: absolute;
  width: clamp(90px, 27vw, 180px);
  height: clamp(90px, 27vw, 180px);
  border-radius: clamp(6px, 1.5vw, 10px);
  box-shadow: inset 0 0 clamp(5px, 1.5vw, 10px) rgba(0, 0, 0, 0.2);
}

/* Red Zone (Top Left) */
.red-zone {
  top: 0;
  left: 0;
  width: clamp(90px, 27vw, 180px);
  height: clamp(90px, 27vw, 180px);
  background: linear-gradient(135deg, #ff6666, #ff0000);
}

/* Green Zone (Top Right) */
.green-zone {
  top: 0;
  right: 0;
  width: clamp(90px, 27vw, 180px);
  height: clamp(90px, 27vw, 180px);
  background: linear-gradient(135deg, #00e676, #00a152);
}

/* Yellow Zone (Bottom Left) */
.yellow-zone {
  bottom: 0;
  left: 0;
  width: clamp(90px, 27vw, 180px);
  height: clamp(90px, 27vw, 180px);
  background: linear-gradient(135deg, #fff176, #fbc02d);
}

/* Blue Zone (Bottom Right) */
.blue-zone {
  bottom: 0;
  right: 0;
  width: clamp(90px, 27vw, 180px);
  height: clamp(90px, 27vw, 180px);
  background: linear-gradient(135deg, #64b5f6, #1565c0);
}

.center-square {
  position: absolute;
  top: 50%;
  left: 50%;
  width: clamp(45px, 13.5vw, 90px);
  height: clamp(45px, 13.5vw, 90px);
  transform: translate(-50%, -50%);
  border: clamp(2px, 0.75vw, 3px) solid #000;
  border-radius: clamp(4px, 1vw, 8px);
  background: linear-gradient(135deg, #fff, #ddd);
  box-shadow: inset 0 0 clamp(2px, 0.8vw, 5px) rgba(0, 0, 0, 0.3);
}
