:root{
  --create-panel-bg: rgba(0,0,0,0.45);
}

/* Scoped overrides and layout tweaks for Create Tournament page */
.tournament-inner{
  max-width:1200px;
  padding: clamp(18px, 3.2vw, 36px);
  background: linear-gradient(180deg, rgba(0,0,0,0.48), rgba(0,0,0,0.6));
}

.tournament-art{ margin: 12px 0 6px 0; }

.tournament-art > div{ max-width:520px; margin:0 auto; }

/* Keep the page title on one line where possible */
.tournament-title{ white-space:nowrap; display:inline-block; padding: 24px 56px; font-size: clamp(42px,7.9vw,60px); border-radius:22px; cursor:pointer; }


.input-label{
  font-size: 50px;
  margin-bottom: 12px;
  font-weight:800;
  color: #fff;
}

.input-field{
  padding:18px 21px;
  font-size:2rem;
  border-radius:12px;
  width:100%;
  max-width:600px;
  background: rgba(255,255,255,0.06);
  border: 2px solid rgba(255,255,255,0.06);
  color: #fff;
}

.input-field:focus{ outline:none; border-color: rgba(255,235,0,0.95); box-shadow: 0 6px 18px rgba(0,0,0,0.45); }

/* make the create controls group clearer */
.tournament-buttons .row{ gap:8px; justify-content:center; }

/* option group using radio inputs */
.players-options{ gap:12px; }
.player-option{
  display:inline-flex;
  align-items:center;
  gap: 30px;
  padding:20px 28px;
  font-weight:800;
  background: linear-gradient(180deg,var(--yellow),#ffd300);
  border:6px solid rgba(0,0,0,0.5);
  color:#111;
  border-radius:24px;
  cursor:pointer;
}
.player-option input[type="radio"]{ width:18px; height:18px; }
.player-option{ font-size: 1.5rem; }
.player-option.active{ box-shadow: 0 10px 0 rgba(0,0,0,0.28); transform: translateY(-2px); }
.player-option.active{ box-shadow: 0 10px 0 rgba(0,0,0,0.28); transform: translateY(-2px); }

/* Reset and Back should be small and consistent */
.small-btn, .back-btn{ min-width:600px; padding:24px 16px; border-radius:18px; }
.reset-btn{ background: linear-gradient(180deg,#ff6a00,#ff3b00); color:#fff; border:2px solid rgba(0,0,0,0.35); font-size:1.5rem; }
/* reduce space below reset row so Reset sits closer to Create/Back */
.reset-row{ margin-bottom:0px; }

/* larger create button */
.create-btn{ min-width:360px; padding:14px 22px; border-radius:22px; background: linear-gradient(180deg,var(--yellow),#ffd300); color:#111; border:3px solid rgba(0,0,0,0.5); }

/* Created info and action buttons */
.created-container{ margin-top:14px; color:#dfffd6; text-align:center; }
.created-info{ font-size:1.6rem; font-weight:800; margin-bottom:10px; }
.created-info strong{ display:inline-block; margin-left:8px; padding:8px 12px; background: rgba(0,0,0,0.36); color:#fff; border-radius:8px; font-size:1.2rem; }
.created-actions{ margin-top:8px; }
.large-action{ min-width:300px; padding:14px 20px; font-size:1.25rem; border-radius:16px; }


@media (max-width:920px){
  .tournament-art > div{ max-width:280px; }
  .input-label{ font-size:18px; }
  .tournament-title{ white-space:normal; }
  .create-btn{ min-width:100%; }
  .players-options{ width:100%; display:flex; gap:8px; }
  .player-option{ font-size: 1.2rem; padding:12px; }
  .small-btn, .back-btn{ min-width:100%; }
}
