.dice-button {
  width: 90px;
  height: 90px;
  background: white;
  border: 3px solid #000;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease;
  box-shadow: 0 4px 10px rgba(0,0,0,0.3);
}

.dice-button:hover {
  transform: scale(1.05);
}

.dice-button:active {
  transform: scale(0.95);
}

.dice-button.rolling {
  animation: shake 0.6s ease infinite alternate;
}

@keyframes shake {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(10deg); }
  50% { transform: rotate(-10deg); }
  75% { transform: rotate(10deg); }
  100% { transform: rotate(0deg); }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.8), 0 0 60px rgba(255, 165, 0, 0.5);
  }
  50% {
    transform: scale(1.1);
    box-shadow: 0 0 40px rgba(255, 215, 0, 1), 0 0 80px rgba(255, 165, 0, 0.7);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 30px rgba(255, 215, 0, 0.8), 0 0 60px rgba(255, 165, 0, 0.5);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(60px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.dot-container {
  position: relative;
  width: 60%;
  height: 60%;
}

.dot {
  width: 12px;
  height: 12px;
  background: #000;
  border-radius: 50%;
  position: absolute;
}

/* Pip positions (1–6) */
.dot-1 { top: 50%; left: 50%; transform: translate(-50%, -50%); }
.dot-2 { top: 20%; left: 20%; }
.dot-3 { top: 20%; right: 20%; }
.dot-4 { bottom: 20%; left: 20%; }
.dot-5 { bottom: 20%; right: 20%; }
.dot-6 { top: 50%; left: 20%; transform: translateY(-50%); }

/* Hide all dots initially */
.dot { opacity: 0; }

/* Show dots based on dice value */
.value-1 .dot-1 { opacity: 1; }
.value-2 .dot-2, .value-2 .dot-5 { opacity: 1; }
.value-3 .dot-1, .value-3 .dot-2, .value-3 .dot-5 { opacity: 1; }
.value-4 .dot-2, .value-4 .dot-3, .value-4 .dot-4, .value-4 .dot-5 { opacity: 1; }
.value-5 .dot-1, .value-5 .dot-2, .value-5 .dot-3, .value-5 .dot-4, .value-5 .dot-5 { opacity: 1; }
.value-6 .dot-2, .value-6 .dot-3, .value-6 .dot-4, .value-6 .dot-5, .value-6 .dot-6 { opacity: 1; }
