/* Background setup */
.login-page {
  height: 100vh;
  width: 100%;
  background: url("../assets/bg.jpg") no-repeat center center/cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
  padding: 16px;
  box-sizing: border-box;
}

/* Game Title — mobile first */
.title {
  font-size: clamp(32px, 12vw, 120px);
  font-weight: 900;
  color: #ffeb00;
  text-shadow: 0 0 20px rgba(255, 255, 0, 0.9);
  margin-bottom: clamp(20px, 5vw, 48px);
  text-align: center;
  word-break: break-word;
}

/* Login box — mobile-friendly */
.login-box {
  background: rgba(0, 0, 0, 0.95);
  padding: clamp(20px, 6vw, 80px) clamp(16px, 6vw, 60px);
  border-radius: 20px;
  box-shadow: 0 0 30px rgba(255, 255, 0, 0.6);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: clamp(280px, 95vw, 600px);
  max-width: 100%;
  box-sizing: border-box;
}

/* Input styling — mobile-optimized */
.login-box input {
  width: 100%;
  padding: clamp(12px, 4vw, 24px) clamp(12px, 4vw, 20px);
  margin: clamp(10px, 3vw, 20px) 0;
  border: none;
  border-radius: 12px;
  font-size: clamp(14px, 4vw, 18px);
  outline: none;
  box-sizing: border-box;
}

/* Login button */
.login-btn {
  width: 100%;
  padding: clamp(14px, 4vw, 24px);
  background: #ffeb00;
  border: none;
  border-radius: 12px;
  font-size: clamp(16px, 4vw, 24px);
  font-weight: 900;
  color: black;
  cursor: pointer;
  margin-top: clamp(15px, 4vw, 30px);
  transition: 0.12s;
  box-sizing: border-box;
}

.login-btn:hover {
  background: #ffcc00;
  transform: scale(1.02);
}

/* Server info — mobile-friendly */
.server-info {
  margin-bottom: clamp(12px, 3vw, 24px);
  color: #fff;
  font-size: clamp(12px, 3vw, 16px);
  text-align: center;
  word-break: break-word;
}

.server-info button {
  margin-left: 4px;
  font-size: inherit;
  background: none;
  border: none;
  color: #ffeb00;
  cursor: pointer;
  text-decoration: underline;
}

/* Register section — mobile-optimized */
.register-section {
  margin-top: clamp(15px, 4vw, 30px);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: clamp(8px, 2vw, 16px);
  font-size: clamp(14px, 4vw, 18px);
  color: white;
  text-align: center;
}

.login-box .register-section .register-btn {
  background: #ffcc00;
  border: none;
  border-radius: 12px;
  padding: clamp(10px, 3vw, 16px) clamp(16px, 4vw, 32px);
  font-weight: 900;
  font-size: clamp(14px, 4vw, 18px);
  line-height: 1.2;
  display: inline-block;
  width: auto;
  white-space: nowrap;
  cursor: pointer;
  transition: 0.08s;
}

.register-btn:hover {
  background: #ffcc00;
  transform: scale(1.05);
}

/* Tablet and larger screens */
@media (min-width: 600px) {
  .title {
    font-size: clamp(48px, 12vw, 120px);
    margin-bottom: clamp(30px, 6vw, 60px);
  }

  .login-box {
    padding: clamp(30px, 8vw, 80px) clamp(30px, 8vw, 70px);
    width: clamp(350px, 80vw, 600px);
  }

  .login-box input {
    padding: clamp(14px, 3vw, 24px);
    font-size: clamp(16px, 3vw, 20px);
    margin: clamp(12px, 2vw, 24px) 0;
  }

  .login-btn {
    padding: clamp(16px, 3vw, 24px);
    font-size: clamp(18px, 3vw, 24px);
    margin-top: clamp(20px, 4vw, 30px);
  }

  .register-section {
    flex-direction: row;
    gap: clamp(12px, 3vw, 24px);
    margin-top: clamp(20px, 5vw, 40px);
  }

  .server-info {
    font-size: clamp(14px, 2.5vw, 18px);
  }
}

/* Large desktop */
@media (min-width: 1024px) {
  .login-box {
    width: clamp(400px, 70vw, 700px);
  }
}
