:root{
  --panel-yellow: #ffea00;
}
.generatekey-container{
  min-height:100vh;
  display:flex;
  align-items:center;
  justify-content:center;
  position:relative;
  padding: clamp(16px, 4vw, 40px);
  box-sizing:border-box;
  background-image: url("../assets/bg.jpg");
  background-size: cover;
  background-position: center;
}
.generatekey-container::before{
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(6,8,20,0.55);
  pointer-events: none;
}
.generatekey-inner{
  position: relative;
  text-align:center;
  padding: clamp(28px, 5vw, 64px);
  border-radius:20px;
  width:860px;
  max-width:96%;
  background: linear-gradient(180deg, rgba(0,0,0,0.5), rgba(0,0,0,0.65));
  box-shadow: 0 48px 140px rgba(0,0,0,0.76), 0 12px 0 rgba(0,0,0,0.32);
  color: #fff;
  backdrop-filter: blur(6px) saturate(120%);
}
.generatekey-title{
  display:block;
  margin: 0 auto;
  background: var(--panel-yellow);
  color: #000;
  padding: 14px 28px;
  border-radius:14px;
  font-weight:900;
  font-size: clamp(24px, 5.8vw, 44px);
  box-shadow: 0 10px 0 rgba(0,0,0,0.6);
}
.generatekey-actions{ margin-top: 22px; display:flex; gap:14px; justify-content:center; }
.generatekey-keybox{ margin-top: 18px; display:block; padding:40px 48px; background: rgba(255,255,255,0.04); border-radius:14px; text-align:center; max-width:760px; margin-left:auto; margin-right:auto; }
.generatekey-key{ font-family: monospace; font-size: 56px; letter-spacing: 6px; color:#fff; }
.generatekey-small{ font-size: clamp(18px, 2.2vw, 28px); color: rgba(255,255,255,0.98); margin-top:12px; font-weight:700; }
.generatekey-btn{ padding:28px 44px; border-radius: 18px; font-weight:900; cursor:pointer; border: none; background: linear-gradient(180deg,var(--panel-yellow),#ffd300); color:#111; box-shadow: 0 16px 0 rgba(0,0,0,0.38); font-size: 44px; }
.generatekey-btn:disabled{ opacity:0.6; cursor:not-allowed; }
.generatekey-back{ background: transparent; border: 2px solid rgba(255,255,255,0.08); color: #fff; }

@media (max-width:720px){
  .generatekey-inner{ width:94%; padding:18px; }
  .generatekey-title{ font-size: 20px; padding:10px 16px; }
  .generatekey-key{ font-size: 20px; }
  .generatekey-btn{ font-size: 16px; padding:12px 18px; }
  .generatekey-small{ font-size: 14px; }
}
