.lobby-container {
  height: 100vh;
  width: 100%;
  background: url("../assets/bg.jpg") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  color: white;
}

.lobby-box {
  background: rgba(0, 0, 0, 1.5);
  border: 6px solid #ffcc00;
  border-radius: 50px;
  padding: 120px 160px;
  text-align: center;
  box-shadow: 0 0 80px #ffcc00;
  backdrop-filter: blur(20px);
  animation: fadeIn 0.6s ease;
  min-width: 960px;
}

@keyframes fadeIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.lobby-title {
  font-size: 5.2rem;
  font-weight: 700;
  color: #00e0ff;
  text-shadow: 0 0 20px #00e0ff;
  margin-bottom: 50px;
}

.room-code {
  font-size: 4rem;
  color: #ffcc00;
  font-weight: 600;
  margin-bottom: 30px;
}

.room-code span {
  background: #ffcc00;
  color: #000;
  padding: 12px 28px;
  border-radius: 20px;
  font-weight: bold;
}

.player-list {
  background: rgba(255, 255, 255, 0.12);
  border: 6px solid #ffcc00;
  border-radius: 40px;
  padding: 40px;
  margin-bottom: 70px;
  box-shadow: inset 0 0 40px #ffcc00;
}

.player-list h2 {
  color: #ffcc00;
  font-size: 3rem;
  margin-bottom: 40px;
}

.player-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.player-list li {
  margin: 10px 0;
  font-size: 2rem;
  font-weight: 600;
  color: #fff;
  text-shadow: 0 0 8px #00ffff;
}

.lobby-buttons {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.start-btn {
  background: linear-gradient(90deg, #00ff66, #00cc44);
  border: none;
  color: white;
  padding: 30px 70px;
  border-radius: 28px;
  font-size: 2rem;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 0 30px #00ff66;
  transition: 0.3s ease;
}

.start-btn:hover {
  background: linear-gradient(90deg, #00cc44, #009933);
  box-shadow: 0 0 25px #00ff66;
}

.start-btn.disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background: gray;
  box-shadow: none;
}

.back-btn {
  background: linear-gradient(90deg, #ff6600, #ff3300);
  border: none;
  color: white;
  padding: 15px 35px;
  border-radius: 14px;
  font-size: 2rem;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 0 15px #ff6600;
  transition: 0.3s ease;
}

.back-btn:hover {
  background: linear-gradient(90deg, #ff8533, #ff6600);
  box-shadow: 0 0 25px #ff9933;
}
