.localplay-container {
  height: 100vh;
  background-image: url("../assets/bg.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.localplay-content {
  text-align: center;
}

.localplay-title {
  background-color: #ffd400;
  color: black;
  padding: clamp(12px, 3vw, 26px) clamp(20px, 5vw, 48px);
  border-radius: 30px;
  margin-bottom: clamp(14px, 3.5vw, 32px);
  font-weight: 900;
  font-size: clamp(2rem, 7vw, 4rem);
}

.localplay-subtitle {
  margin-bottom: clamp(12px, 3vw, 20px);
  color: white;
  font-size: clamp(3rem, 3.8vw, 1.6rem);
}

/* scope buttons to this page */
.localplay-content button,
.localplay-content .exit-main-btn {
  display: block;
  width: clamp(220px, 64vw, 900px);
  margin: 30px auto;
  padding: clamp(12px, 3.6vw, 26px) clamp(18px, 5.5vw, 44px);
  border: none;
  border-radius: 22px;
  background-color: #ffd400;
  color: black;
  font-weight: 900;
  cursor: pointer;
  transition: transform 0.16s ease-in-out, background 0.12s ease-in-out;
  font-size: clamp(1.05rem, 4.6vw, 1.9rem);
}

.localplay-content button:hover,
.localplay-content .exit-main-btn:hover {
  background-color: #b33a3a; /* darker red on hover */
  transform: translateY(-2px) scale(1.02);
}

/* keep a slightly stronger visual for main exit */
.localplay-content .exit-main-btn {
  box-shadow: 0 8px 18px rgba(0,0,0,0.22);
  font-size: clamp(1.15rem, 5vw, 2rem);
  background-color: #d64545; /* red */
  color: #fff;
  border: 2px solid #b33a3a;
}

/* top-left exit button (scoped to LocalPlay) */
.localplay-container .profile-section .exit-btn {
  position: absolute;
  top: 18px;
  left: 18px;
  z-index: 999;
  background: #d64545;
  color: #fff;
  border: 6px solid #b33a3a;
  min-width: 80px;
  min-height: 80px;
  padding: clamp(10px, 2.5vw, 18px);
  border-radius: 12px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  font-size: clamp(1.4rem, 3.6vw, 2.8rem);
}

.localplay-container .profile-section .exit-btn:hover {
  background: #b33a3a;
}
