.server-settings-container {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12px;
  color: white;
  min-height: 100vh;
}

.server-settings-container h2 {
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 30px;
  text-align: center;
}

.settings-section {
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
}

.settings-section label {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 8px;
  color: #fff;
}

.settings-section input {
  padding: 14px;
  font-size: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-family: monospace;
  transition: all 0.2s;
}

.settings-section input:focus {
  outline: none;
  border-color: #fff;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

.settings-section input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.hint {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.75);
  margin-top: 6px;
}

.current-url {
  background: rgba(0, 0, 0, 0.2);
  padding: 12px;
  border-radius: 6px;
  font-family: monospace;
  font-size: 0.95rem;
  word-break: break-all;
  border-left: 4px solid #4ade80;
}

.message {
  padding: 14px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
}

.message.success {
  background: rgba(74, 222, 128, 0.2);
  border: 2px solid #4ade80;
  color: #86efac;
}

.message.error {
  background: rgba(239, 68, 68, 0.2);
  border: 2px solid #ef4444;
  color: #fca5a5;
}

.message.info {
  background: rgba(59, 130, 246, 0.2);
  border: 2px solid #3b82f6;
  color: #93c5fd;
}

.settings-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 30px;
}

.settings-actions button {
  flex: 1;
  padding: 14px;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.settings-actions button:first-child {
  background: #ffd400;
  color: #000;
}

.settings-actions button:first-child:hover:not(:disabled) {
  background: #ffcc00;
  transform: translateY(-2px);
}

.settings-actions button.btn-secondary {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  border: 2px solid rgba(255, 255, 255, 0.4);
}

.settings-actions button.btn-scan {
  background: #4ade80;
  color: #000;
  border: none;
}

.settings-actions button.btn-scan:hover:not(:disabled) {
  background: #22c55e;
  transform: translateY(-2px);
}

.settings-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 30px;
}

@media (max-width: 600px) {
  .settings-actions {
    flex-direction: column;
  }
}
.settings-actions button.btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.3);
  border-color: #fff;
}

.settings-actions button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.settings-info {
  background: rgba(0, 0, 0, 0.2);
  padding: 20px;
  border-radius: 8px;
  border-left: 4px solid #ffd400;
}

.settings-info h3 {
  font-size: 1.1rem;
  margin-bottom: 12px;
  color: #ffd400;
}

.settings-info ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.settings-info li {
  padding: 8px 0;
  line-height: 1.5;
  font-size: 0.95rem;
}

.settings-info strong {
  color: #fff;
}

@media (max-width: 600px) {
  .server-settings-container {
    padding: 16px;
    min-height: auto;
  }

  .server-settings-container h2 {
    font-size: 1.5rem;
  }

  .settings-actions {
    flex-direction: column;
  }
}
