
.upcoming-container { padding: 45px 30px; display:flex; flex-direction:column; align-items:center; }
.upcoming-title{
	margin-bottom: 62px;
	background:#ffd400;
	color:#0b3b66;
	padding:15px 25px;
	border-radius:10px;
	font-weight:800;
	box-shadow:0 5px 18px rgba(0,0,0,0.25);
}
.upcoming-title{ font-size:110px; }
.upcoming-list{ width:100%; max-width:1500px; display:flex; flex-direction:column; gap:25px; align-items:center; }
.upcoming-item{ display:flex; align-items:center; justify-content:space-between; padding:18px 23px; }
.upcoming-footer{ margin-top:25px; display:flex; gap:15px; }

/* Tournaments table styles */
.tournaments-table{
	width: 100%;
	max-width:2000px;
	border-collapse:collapse;
	background:#fff; /* white background */
	color:#222; /* dark text */
	box-shadow:0 10px 35px rgba(3,18,38,0.18);
	border-radius:12px;
	overflow:hidden;
	margin: 0 auto;
	font-size:25px; /* 1.25x */
}
.tournaments-table thead th{
	text-align:left;
	padding:22px 29px; /* ~1.25x */
	font-weight:800;
	font-size:28px; /* ~1.25x */
	background:#fafafa;
	color:#111;
	border-bottom:1px solid #eee;
}
.tournaments-table tbody td{
	padding:19px 25px; /* ~1.25x */
	border-bottom:1px solid #f2f2f2;
	vertical-align:middle;
	font-size:25px; /* 1.25x */
	font-weight:700;
}
.tournaments-table tbody tr:hover{
	background:#f7fbff;
}
.tournaments-table tbody tr.selected{
	background:#e8f0ff;
}
.tournaments-table .t-name{ font-weight:800; color:#0b3b66; font-size:28px; }
.tournaments-table .t-rounds, .tournaments-table .t-total, .tournaments-table .t-spots, .tournaments-table .t-start{ color:#333; font-size:25px; }

/* table-actions removed — per-row join buttons used instead */
.table-actions{ display:none; }

/* Button tweaks for table */
.btn.small{ padding:8px 10px; font-size:0.95rem; }
.btn.danger{ background:#e85c5c; color:#fff; border:none; box-shadow:0 2px 0 rgba(0,0,0,0.06); }
.btn.danger.small{ background:#d64545; }

.upcoming-container{ color: #0b3b66; }

/* Theme buttons */
.btn.join-selected{ background:#ffd400; color:#0b3b66; border:none; padding:15px 29px; font-weight:800; border-radius:8px; font-size:25px; }

.tournaments-table td.actions{ text-align:right; white-space:nowrap; }
.tournaments-table td.actions .btn{ vertical-align:middle; }

/* Footer buttons */
.upcoming-footer .btn{ padding:15px 22px; border-radius:10px; }
.upcoming-footer .join-code-btn{ background:#ffd400; color:#0b3b66; }
.upcoming-footer .back-btn{ background:#ff6b1a; color:#fff; padding:15px 35px; min-width:1500px; text-align:center; font-weight:800; }

/* Small screens: allow stacked mobile view */
@media (max-width: 720px) {
	.tournaments-table thead { display: none; }
	.tournaments-table, .tournaments-table tbody, .tournaments-table tr, .tournaments-table td { display: block; width: 100%; }
	.tournaments-table tbody tr { margin-bottom: 12px; border-radius:10px; background:#fff; box-shadow:0 5px 12px rgba(0,0,0,0.06); }
	.tournaments-table td { padding:15px; border-bottom:0; }
	.table-actions { justify-content: center; }
	.upcoming-list { padding: 0 10px; }
}

