.join-container{ padding:20px; }
.join-meta{ margin-top:16px; }
.join-error{ color: #ffb3b3; margin-top:10px; }

/* Terms modal styles */
.terms-modal-overlay{
	position:fixed;
	left:0;
	top:0;
	right:0;
	bottom:0;
	background:rgba(0,0,0,0.6);
	display:flex;
	align-items:center;
	justify-content:center;
	z-index:9999;
}
.terms-modal{
	background:#fff;
	max-width:1000px; /* increased width */
	width:95%;
	max-height:86vh;
	border-radius:10px;
	padding:22px;
	box-shadow:0 8px 40px rgba(0,0,0,0.45);
	display:flex;
	flex-direction:column;
	font-size: 2rem;
}
.terms-modal h3{ margin:0 0 12px 0; color: #111; font-size: 2.4rem; }
.terms-modal,
.terms-modal * {
	color: #111 !important;
}
.terms-content{
	flex:1 1 auto;
	overflow:auto;
	padding:12px 10px;
	max-height: calc(80vh - 200px); /* ensure content area is scrollable */
	border:1px solid #eee;
	border-radius:4px;
	background:#fafafa;
	text-align: left;
}
.terms-actions{ margin-top:12px; display:flex; flex-direction:column; }
.terms-actions .btn { color: #111; }
.terms-content p{ font-size: 1.1em; line-height:1.6; text-align: left; }
.terms-actions label{ font-size: 1.1em; display:flex; align-items:center; margin-left:28px; gap:12px; position:relative; padding-left:60px; }

/* custom checkbox: visually hide native input and draw our own box via label::before */
.terms-actions label input[type="checkbox"]{
	position: absolute;
	left: 12px;
	top: 50%;
	transform: translateY(-50%);
	width: 40px;
	height: 40px;
	opacity: 0;
	z-index: 10;
	cursor: pointer;
}
/* draw our custom box using span::before so checkmark can be shown via sibling selector reliably */
.terms-actions label span::before{
	content: '';
	position: absolute;
	left: 12px;
	top: 50%;
	transform: translateY(-50%);
	width: 40px;
	height: 40px;
	border: 2px solid #d6d6d6;
	background: #fff;
	border-radius: 6px;
	box-shadow: inset 0 1px 0 rgba(0,0,0,0.02);
	z-index: 5;
}
/* show checkmark when checked */
.terms-actions label input[type="checkbox"]:checked + span::before{
	/* show checkmark when checked */
	content: '✔';
	color: #064e3b;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 22px;
}
.terms-actions label input[type="checkbox"]:focus + span::before{
	outline: 3px solid rgba(6,78,59,0.12);
	border-radius: 6px;
}

