body {
  background: radial-gradient(circle at center, #021024, #000);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.ludo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ludo-board {
  width: 500px;
  height: 500px;
  background: white;
  display: grid;
  grid-template-columns: repeat(15, 1fr);
  grid-template-rows: repeat(15, 1fr);
  border: 6px solid white;
  position: relative;
  gap: 0; /* ensure no gaps between grid cells */
}

.ludo-board {
  position: relative;
  z-index: 1; /* ✅ Ensures board stays below tokens */
}


/* ==== Bases (solid, no inner grid lines) ==== */
.base {
  position: relative;
  overflow: hidden;
}

.red-base {
  grid-area: 1 / 1 / 7 / 7;
  background: #e74c3c;
  border: 3px solid black;
}

.green-base {
  grid-area: 1 / 10 / 7 / 16;
  background: #27ae60;
  border: 3px solid black;
}

.yellow-base {
  grid-area: 10 / 10 / 16 / 16;
  background: #f1c40f;
  border: 3px solid black;
}

.blue-base {
  grid-area: 10 / 1 / 16 / 7;
  background: #2980b9;
  border: 3px solid black;
}

/* Hide the inner grid lines that overlap bases */
.ludo-board > div {
  border: none;
}

/* Keep path cell borders visible */
.path {
  background: white;
  border: 1px solid black;
  z-index: 2;
}

/* ==== Center Home ==== */
.center-home {
  grid-area: 7 / 7 / 10 / 10;
  position: relative;
  border: 1px solid black;
  z-index: 3;
}

.triangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}

.triangle.red {
  border-width: 45px 45px 0 0;
  border-color: #e74c3c transparent transparent transparent;
}

.triangle.green {
  border-width: 0 45px 45px 0;
  border-color: transparent #27ae60 transparent transparent;
  top: 0;
  right: 0;
}

.triangle.yellow {
  border-width: 0 0 45px 45px;
  border-color: transparent transparent #f1c40f transparent;
  bottom: 0;
  right: 0;
}

.triangle.blue {
  border-width: 45px 0 0 45px;
  border-color: transparent transparent transparent #2980b9;
  bottom: 0;
  left: 0;
}

/* ==== Paths ==== */
.red-path {
  grid-area: 7 / 3 / 10 / 7;
}

.green-path {
  grid-area: 3 / 10 / 7 / 13;
}

.yellow-path {
  grid-area: 10 / 10 / 13 / 13;
}

.blue-path {
  grid-area: 10 / 3 / 13 / 7;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100vh;
  background: url('../assets/bg.jpg') center/cover no-repeat;
}

.exit-btn {
  position: absolute;
  top: 30px;
  left: 30px;
  background: white;
  border: 3px solid black;
  border-radius: 10px;
  padding: 16px 28px;
  font-weight: bold;
  font-size: 1.4rem;
  cursor: pointer;
  z-index: 10;
}

.profiles-container {
  display: flex;
  gap: 30px;
  margin-bottom: 20px;
}

.player-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s;
}

.player-profile.active {
  transform: scale(1.2);
}

.player-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 3px solid black;
  object-fit: cover;
}

.dice-container {
  position: absolute;
  bottom: 60px;
  display: flex;
  justify-content: center;
}

.dice-btn {
  font-size: 2rem;
  color: white;
  border: 4px solid black;
  border-radius: 14px;
  padding: 20px 30px;
  cursor: pointer;
  box-shadow: 3px 3px 12px rgba(0,0,0,0.5);
}

/* add to Dice.css or LudoBoard.css (the file must be imported by Game.jsx) */

@keyframes bounce {
  0%   { transform: translate(-50%, -50%) scale(1.03); }
  50%  { transform: translate(-50%, -58%) scale(1.14); }
  100% { transform: translate(-50%, -50%) scale(1.08); }
}

/* selectable token: glow + bounce */
.selectable-token {
  /* strong glow using both box-shadow and filter drop-shadows */
  box-shadow:
    0 6px 18px rgba(255,255,255,0.95),
    0 0 28px rgba(255,215,0,0.95),
    0 12px 40px rgba(255,215,0,0.25);
  filter:
    drop-shadow(0 0 12px rgba(255,255,255,0.95))
    drop-shadow(0 0 22px rgba(255,215,0,0.9));
  animation: bounce 0.56s ease-in-out infinite;
  transform-origin: 50% 50%;
  /* ensure the element visually sits above others */
  position: relative;
  z-index: 999 !important;
}

.token {
  pointer-events: auto;
  position: absolute; /* ✅ critical! */
  z-index: 9999 !important; /* ✅ highest layer */
}

/* Token moving animation - smooth step by step */
.token-moving {
  animation: moveSmooth 0.3s ease-in-out forwards !important;
}

@keyframes moveSmooth {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  25% {
    transform: translate(-50%, -55%) scale(1.08);
  }
  75% {
    transform: translate(-50%, -45%) scale(1.08);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

/* Token styling improvements */
img.token {
  transition: filter 0.2s ease, transform 0.15s ease;
  cursor: pointer;
  border-radius: 8px;
}

img.token:hover {
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.6)) brightness(1.1);
  transform: scale(1.08);
}

img.selectable-token {
  animation: tokenGlow 1s ease-in-out infinite;
  cursor: pointer;
  border-radius: 8px;
}

@keyframes tokenGlow {
  0%, 100% {
    filter: drop-shadow(0 0 12px rgba(255, 255, 0, 0.9)) brightness(1);
  }
  50% {
    filter: drop-shadow(0 0 20px rgba(255, 255, 0, 1)) brightness(1.15);
  }
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.08);
    opacity: 0.9;
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}