.ongo-container {
  max-width: 900px;
  margin: 24px auto;
  padding: 16px;
  /* scale page modestly for readability */
  zoom: 1.2; /* Chrome/Edge */
  -moz-transform: scale(1.2); /* Firefox fallback */
  -moz-transform-origin: 0 0;
  transform: scale(1.2);
  transform-origin: 0 0;
}

.ongo-header {
  text-align: center;
  font-size: 1.9rem;
  margin-bottom: 12px;
  font-weight: 800;
  color: #ffffff;
  text-shadow: 0 2px 8px rgba(0,0,0,0.6);
}

.ongo-card {
  background: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  color: #333;
}

.ongo-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.ongo-main-row {
  display: flex;
  gap: 16px;
  align-items: stretch; /* ensure siblings match height */
}

.back-vertical {
  background: #ff6b00;
  color: #fff;
  border: none;
  border-radius: 8px;
  width: 56px;
  min-width: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 8px 6px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.15);
  cursor: pointer;
  transition: background 120ms ease;
  align-self: stretch; /* stretch to match the content height */
}

.back-vertical:hover { background: #ff8a3a; }

.back-letter {
  display: block;
  line-height: 1;
  font-weight: 700;
  font-size: 16px;
  margin: 2px 0;
}

.ongo-content { flex: 1 1 auto; }

.ongo-rounds {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.round {
  border: 1px solid #ddd;
  padding: 12px;
  border-radius: 8px;
  background: #fafafa;
}

.round-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.groups {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.group {
  flex: 1 1 220px;
  border: 1px solid #eee;
  padding: 10px;
  border-radius: 6px;
  background: #fff;
}

.group-label {
  font-weight: 600;
  margin-bottom: 8px;
}

.group-players {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 8px;
}

.player { color: #222; }
.player-name { padding-left: 6px; }

.group-winner { font-style: italic; color: #555; }

.round-placeholder { color: #666; padding: 12px; }

.btn.next-round { background: #2d89ff; color: #fff; border: none; padding: 6px 10px; border-radius: 4px; cursor: pointer; }

.btn.send-reminder { background: #28a745; color: #fff; border: none; padding: 6px 12px; border-radius: 4px; cursor: pointer; margin-left: 8px; transition: background 120ms ease; }
.btn.send-reminder:hover { background: #34d65e; }
.btn.send-reminder:active { background: #20752a; }

.tournament-info { margin: 12px 0; text-align: left; }
.info-row { display: flex; justify-content: space-between; padding: 8px 0; border-bottom: 1px solid #e6e6e6; }
.info-row:last-child { border-bottom: none; }
.info-label { font-weight: 600; color: #333; }
.info-value { color: #666; }

.start-time-input { display: flex; gap: 8px; margin-top: 12px; align-items: center; flex-wrap: wrap; }
.start-time-input input { padding: 8px; border: 1px solid #ccc; border-radius: 4px; font-size: 14px; }
.btn.set-start-time { background: #6610f2; color: #fff; border: none; padding: 8px 16px; border-radius: 4px; cursor: pointer; transition: background 120ms ease; }
.btn.set-start-time:hover:not(:disabled) { background: #7c25f2; }
.btn.set-start-time:disabled { background: #ccc; cursor: not-allowed; }

.view-players { background:#2d89ff; color:#fff; border:none; padding:8px 12px; border-radius:6px; cursor:pointer; margin-top:8px; }
.view-players:hover { background:#3b9bff; }

.joined-players-list { margin-top:10px; display:flex; flex-direction:column; gap:6px; align-items:center; }
.player-entry { background: #fff; padding:6px 10px; border-radius:4px; border:1px solid #e6e6e6; width:100%; max-width:560px; box-shadow: 0 1px 0 rgba(0,0,0,0.03); text-align:center; }

