.root {
}
:root {
  --accent: #ffd400;
  --accent-dark: #ffcc00;
  --accent-contrast: #000;
  --gap: 1rem;
}

html {
  /* Scale base font-size between small and large phones (increase overall sizing) */
  font-size: clamp(16px, 4.5vw, 22px);
}

.mainmenu-container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../assets/bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
  padding: clamp(10px, 3vw, 20px);
  box-sizing: border-box;
}

/* ✅ Profile top-right */
.profile-section {
  position: absolute;
  top: clamp(12px, 4vw, 30px);
  right: clamp(14px, 4vw, 36px);
}

.profile-icon {
  width: clamp(72px, 18vw, 120px);
  height: clamp(72px, 18vw, 120px);
  border-radius: 50%;
  cursor: pointer;
  border: 4px solid var(--accent);
  transition: transform 0.16s ease-in-out, box-shadow 0.12s ease-in-out;
  box-shadow: 0 6px 16px rgba(0,0,0,0.28);
}

.profile-icon:hover {
  transform: scale(1.04);
}

/* Admin trash (mirror of profile icon, left side) */
.trash-section {
  position: absolute;
  top: clamp(12px, 4vw, 30px);
  left: clamp(14px, 4vw, 36px);
}

.trash-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0,0,0,0.14);
}

.trash-icon svg {
  width: 52%;
  height: 52%;
  color: var(--accent);
}

/* ✅ Dropdown menu */
.dropdown-menu {
  position: absolute;
  top: calc(clamp(56px, 16vw, 110px) + 8px);
  right: 0;
  background: rgba(0, 0, 0, 0.94);
  border: 1px solid var(--accent);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 0.65rem;
  padding: 1rem;
  z-index: 50;
  min-width: clamp(200px, 52vw, 360px);
}

.dropdown-username {
  color: var(--accent);
  font-weight: 800;
  margin-bottom: 0.35rem;
  border-bottom: 1px solid var(--accent);
  padding-bottom: 0.35rem;
  font-size: clamp(1.05rem, 3.8vw, 1.4rem);
}

.dropdown-menu button {
  background: transparent;
  border: none;
  color: var(--accent);
  font-weight: 800;
  cursor: pointer;
  text-align: left;
  padding: 0.6rem 0.2rem;
  font-size: clamp(1rem, 3.6vw, 1.3rem);
  display: block;
  width: 100%;
  border-radius: 6px;
}

.dropdown-menu button:hover {
  color: white;
  background: rgba(255,255,255,0.04);
}

/* ✅ Perfectly centered main menu layout */
.mainmenu-content {
  position: relative;
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  color: white;
  padding: clamp(10px, 4vw, 24px);
}

/* ✅ Title */
.mainmenu-title {
  font-size: clamp(2rem, 8vw, 4.2rem);
  font-weight: 900;
  letter-spacing: 0.02em;
  background: var(--accent);
  color: var(--accent-contrast);
  /* wider, button-like yellow background */
  padding: clamp(10px, 1.8vw, 18px) clamp(22px, 6vw, 44px);
  border-radius: 0.8rem;
  display: block;
  width: clamp(260px, 58vw, 700px);
  max-width: 90%;
  margin: 0.6rem auto 1.1rem auto;
  text-align: center;
}

/* ✅ Centered Ludo image below title */
.mainmenu-image {
  width: clamp(200px, 62vw, 560px);
  height: auto;
  display: block;
  margin: 0 auto 1.6rem auto;
}

/* ✅ Buttons layout */
.mainmenu-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: calc(var(--gap) + 0.25rem);
}

/* Two buttons per row */
.button-row {
  display: flex;
  gap: clamp(10px, 3.5vw, 62px);
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.button-row > button {
  flex: 1 1 auto;
  max-width: clamp(180px, 44vw, 360px);
}

.mainmenu-buttons button:not(.generate-key-btn) {
  background: var(--accent);
  border: none;
  padding: clamp(14px, 4vw, 24px) clamp(22px, 7vw, 44px);
  font-size: clamp(1.05rem, 5vw, 1.6rem);
  font-weight: 900;
  border-radius: 1.4rem;
  cursor: pointer;
  transition: transform 0.18s ease-in-out, background 0.12s ease-in-out;
  min-width: clamp(160px, 42vw, 260px);
  text-align: center;
}

.mainmenu-buttons button:hover {
  background: var(--accent-dark);
  transform: translateY(-2px) scale(1.02);
}

/* ✅ Wider Join Now */
.join-now-btn {
  width: clamp(260px, 72vw, 780px);
  display: block;
  margin: 0.6rem auto 0 auto;
  font-size: clamp(1.2rem, 5.2vw, 1.9rem);
  padding: clamp(14px, 4.2vw, 26px) clamp(24px, 8vw, 48px);
}

/* Larger generate key button (admin) */
.generate-key-btn {
  width: clamp(280px, 48vw, 770px);
  display: block;
  margin: 0.4rem auto 0 auto;
  min-width: unset;
  /* larger text only for admin generate button */
  padding: clamp(14px, 4vw, 26px) clamp(26px, 8vw, 44px);
  font-size: clamp(1.25rem, 5.2vw, 2.2rem);
  border-radius: 1.2rem;
}

.generate-key-btn:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}

/* Small screens tweaks */
@media (max-width: 480px) {
  .mainmenu-title { font-size: clamp(1.6rem, 9.5vw, 2.6rem); }
  .button-row { gap: 12px; }
  .dropdown-menu { right: -4px; }
  .profile-section { right: 10px; }
}

@media (min-width: 1024px) {
  .mainmenu-content { padding: 28px; }
  .mainmenu-title { font-size: 3rem; }
}

/* Coming Soon modal overlay */
.coming-soon-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(4px);
}

.coming-soon-modal {
  background: #fff;
  color: #111;
  padding: 22px 28px;
  border-radius: 12px;
  min-width: 240px;
  max-width: 90%;
  box-shadow: 0 12px 40px rgba(0,0,0,0.35);
  text-align: center;
}

.coming-soon-modal h2 {
  margin: 0 0 10px 0;
  font-size: 1.6rem;
  font-weight: 900;
  letter-spacing: 0.02em;
}

.coming-soon-modal p {
  margin: 0;
  font-size: 1.25rem;
  color: #333;
  font-weight: 700;
}

@media (min-width: 1024px) {
  .coming-soon-modal h2 { font-size: 2rem; }
  .coming-soon-modal p { font-size: 1.3rem; }
}
