.profile-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  color: white;
  font-family: "Poppins", sans-serif;
}

.profile-title {
  background: yellow;
  color: black;
  padding: 36px 84px;
  border-radius: 30px;
  font-weight: 800;
  font-size: 6rem;
  margin-bottom: 60px;
}

.profile-card {
  background: rgba(0, 0, 0, 0.75);
  border-radius: 60px;
  padding: 80px 160px;
  margin: 40px;
  width: 1000px;
  text-align: left;
  box-shadow: 0px 4px 10px rgba(255, 255, 0, 0.3);
}

.profile-card h2 {
  color: yellow;
  margin-bottom: 40px;
}

.profile-card p {
  color: white;
  font-size: 3rem;
  margin: 20px 0;
}

.back-btn {
  background: yellow;
  color: black;
  font-weight: bold;
  padding: 10px 20px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  margin: 24px auto 0;
  transition: all 0.18s ease;
  display: inline-block;
  width: auto;
  min-width: 140px;
  max-width: 1320px;
  text-align: center;
}

.back-btn:hover {
  background: #ffcc00;
  transform: scale(1.06);
}
