html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background: url("../assets/bg.jpg") center center / cover no-repeat fixed;
  background-color: black;
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}

.contact-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-card {
  background-color: rgba(0, 0, 0, 0.8);
  padding: 160px 200px;
  border-radius: 40px;
  box-shadow: 0 0 40px #ffcc00;
  width: 800px;
  text-align: center;
  border: 1px solid rgba(255, 204, 0, 0.4);
  color: white;
}

.contact-card h2 {
  color: #ffcc00;
  margin-top: 0px;
  font-size: 100px;
  font-weight: 700;
}

.contact-card form {
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.contact-card input,
.contact-card textarea {
  background-color: #222;
  border: 1px solid #555;
  border-radius: 10px;
  padding: 12px;
  color: white;
  font-size: 2rem;
  outline: none;
}

.contact-card input:focus,
.contact-card textarea:focus {
  border-color: #ffcc00;
}

.contact-btn {
  background-color: #ffcc00;
  color: black;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  padding: 12px;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

.contact-btn:hover {
  background-color: #ffdf4d;
  transform: scale(1.05);
}

.back-btn {
  margin-top: 45px;
  background: none;
  border: none;
  color: #ffcc00;
  text-decoration: underline;
  cursor: pointer;
  font-size: 0.9rem;
}

.back-btn:hover {
  color: #ffdf4d;
}
