.joinroom-container {
  background: url("../assets/bg.jpg") no-repeat center center/cover;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.joinroom-card {
  background: rgba(0, 0, 0, 0.85);
  border: 6px solid #ffcc00;
  border-radius: 45px;
  padding: 105px 120px;
  text-align: center;
  box-shadow: 0 0 60px #ffcc00;
  color: white;
  width: 750px;
  transition: all 0.3s ease;
}

.joinroom-card:hover {
  box-shadow: 0 0 60px #ffd700;
  transform: scale(1.02);
}

.joinroom-title {
  background-color: #ffcc00;
  color: black;
  padding: 18px 25px;
  border-radius: 12px;
  font-weight: 900;
  font-size: 2rem;
  letter-spacing: 1px;
  margin-bottom: 30px;
  box-shadow: 0 0 25px #ffcc00;
}

.joinroom-img {
  width: 140px;
  height: 140px;
  margin-bottom: 35px;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

.joinroom-input {
  width: 95%;
  padding: 18px;
  border-radius: 12px;
  border: none;
  outline: none;
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 25px;
  box-shadow: 0 0 15px #00ffff;
}

.join-btn {
  width: 100%;
  background-color: #28a745;
  border: none;
  border-radius: 12px;
  color: white;
  padding: 18px 20px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 20px;
  transition: 0.3s;
  box-shadow: 0 0 25px #00ff66;
}

.join-btn:hover {
  background-color: #1f8c3a;
  transform: scale(1.08);
}

.create-btn {
  width: 100%;
  background-color: #ffcc00;
  border: none;
  border-radius: 12px;
  color: black;
  padding: 18px 20px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 20px;
  transition: 0.3s;
  box-shadow: 0 0 25px #ffcc00;
}

.create-btn:hover {
  background-color: #e6b800;
  transform: scale(1.08);
}

.back-btn {
  width: 100%;
  background-color: #007bff;
  border: none;
  border-radius: 12px;
  color: white;
  padding: 18px 20px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: 0 0 25px #3399ff;
}

.back-btn:hover {
  background-color: #0056b3;
  transform: scale(1.08);
}
