.register-page {
  height: 100vh;
  width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

.register-box {
  background: rgba(0, 0, 0, 0.6);
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.6);
}

.register-box h1 {
  color: #ffcc00;
  font-size: 36px;
  margin-bottom: 30px;
  font-weight: 700;
}

.register-box input {
  width: 100%;
  padding: 14px;
  margin: 10px 0;
  border-radius: 10px;
  border: none;
  font-size: 16px;
}

.register-btn {
  width: 100%;
  padding: 14px;
  background-color: #ffcc00;
  color: black;
  font-size: 18px;
  font-weight: 700;
  border: none;
  border-radius: 25px;
  margin-top: 15px;
  cursor: pointer;
  transition: 0.3s;
}

.register-btn:hover {
  transform: scale(1.05);
}

p {
  color: white;
  margin-top: 20px;
  font-size: 18px;
}

.login-btn {
  background-color: #ffcc00;
  color: black;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  padding: 5px 12px;
  margin-left: 8px;
  cursor: pointer;
}
