.my-tournaments-container {
  padding: 24px;
}

.my-tournaments-title {
  font-size: 1.5rem;
  margin-bottom: 8px;
}

.my-tournaments-empty {
  color: #ddd;
  margin-bottom: 12px;
}

.my-tournaments-back {
  display: inline-block;
}

.my-tournaments-back .btn {
  cursor: pointer;
}

/* Table styles moved from inline and TournamentUpcoming.css */
.upcoming-table {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  border-collapse: collapse;
  background-color: #e9ddad;
  color: #000000;
  border: 1px solid #000;
  font-size: 30px;
  line-height: 2.85;
}

.upcoming-table thead .table-title {
  padding: 33px 36px;
  border: 1px solid #000;
  color: #000;
  font-weight: 900;
  text-align: center;
  background: #ffc800;
  font-size: 42px;
}

.upcoming-table thead th {
  text-align: left;
  padding: 24px 30px;
  border: 1px solid #000;
  color: #000;
  font-weight: 900;
  font-size: 30px;
}

/* Prevent wrapping for the 'No. of player' (2nd) and 'Time when start' (3rd) columns */
.upcoming-table thead th:nth-child(2),
.upcoming-table tbody td:nth-child(2),
.upcoming-table thead th:nth-child(3),
.upcoming-table tbody td:nth-child(3) {
  white-space: nowrap;
}

.upcoming-table tbody td {
  padding: 27px 33px;
  border: 1px solid #000;
  color: #000;
  font-weight: 800;
  font-size: 30px;
  vertical-align: middle;
}

.upcoming-table tbody tr:hover {
  background: #f7f7f7;
}

/* Vertical scroll list */
.upcoming-list {
  display: block;
  gap: 24px;
  /* allow the list to expand so document-level scrolling works */
  overflow-y: visible;
  max-height: none;
  padding: 12px 8px 48px;
  box-sizing: border-box;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.upcoming-item {
  width: 100%;
  margin-bottom: 18px;
  box-sizing: border-box;
}

/* entry animation: staggered fade/slide for each tournament card */
.upcoming-item {
  opacity: 0;
  transform: translateY(12px);
  animation: mt-enter 420ms cubic-bezier(.2,.9,.2,1) forwards;
  animation-delay: calc(var(--i, 0) * 90ms);
}

@keyframes mt-enter {
  from { opacity: 0; transform: translateY(12px); }
  to { opacity: 1; transform: translateY(0); }
}

/* scrollbar styling (modern browsers) */
.upcoming-list::-webkit-scrollbar { width: 12px; }
.upcoming-list::-webkit-scrollbar-track { background: transparent; }
.upcoming-list::-webkit-scrollbar-thumb { background: rgba(0,0,0,0.25); border-radius: 8px; }
.upcoming-list::-webkit-scrollbar-thumb:hover { background: rgba(0,0,0,0.35); }

@media (max-width: 800px) {
  .upcoming-list { max-height: none; }
}

/* Page-specific overrides: undo global scaling from Tournament.css so native scrolling works */
.upcoming-container {
  transform: none !important;
  transform-origin: unset !important;
  min-height: auto !important;
  align-items: center !important;
  overflow: visible !important;
}

@media (max-width: 1500px) {
  .upcoming-table { width: 100%; font-size: 27px; }
  .upcoming-table thead .table-title { font-size: 36px; padding: 21px 24px; }
  .upcoming-table thead th { font-size: 24px; padding: 18px 21px; }
  .upcoming-table tbody td { font-size: 24px; padding: 18px 21px; }
}
