/* Admin Tournament Dashboard styles - similar look to Upcoming but focused on started tournaments */

.admin-dashboard { padding: 28px 24px; display: flex; flex-direction: column; gap: 14px; }
.admin-dashboard h2 { margin: 0; font-size: 80px; line-height: 1; color: #0b3b66; background: #ffd400; padding: 16px 22px; border-radius: 8px; font-weight: 900; box-shadow: 0 8px 26px rgba(0,0,0,0.14); }
.admin-dashboard p { margin: 6px 0 12px 0; color: #eef; font-size: 13px; }

/* Page header area: title + controls */
.admin-header { display:flex; align-items:center; justify-content:center; gap:12px; }
.admin-header .left { display:flex; flex-direction:column; align-items:center; text-align:center; }
.admin-header .subtitle { color:#334; font-size:14px; margin-top:6px; }
.admin-header .right { display:flex; gap:8px; align-items:center; }

/* Table wrapper for started tournaments */
.admin-table { width:98%; max-width:1400px; margin: 12px auto 0; }
.admin-table .card { background:#fff; border-radius:12px; box-shadow:0 10px 30px rgba(3,18,38,0.12); overflow:hidden; margin: 0 auto; width: 100%; }
.admin-table table { width:100%; border-collapse:collapse; font-size:36px; }
.admin-table thead th { text-align:left; padding:40px 48px; background:#fafafa; font-weight:900; color:#111; border-bottom:1px solid #eee; font-size:36px; }
.admin-table tbody td { padding:32px 48px; border-bottom:1px solid #f2f2f2; vertical-align:middle; color:#222; font-size:34px; }
.admin-table tbody tr:hover { background:#f7fbff; }
.admin-table .t-name { font-weight:900; color:#0b3b66; font-size:36px; }
.admin-table .t-matches { color:#555; font-size:32px; }
.admin-table .t-actions { text-align:right; white-space:nowrap; }

.admin-table .small { padding:6px 10px; font-size:13px; }


.admin-empty { color: #ddd; padding: 18px; background: rgba(0,0,0,0.06); border-radius: 8px; }


.admin-tournament { background: #fff; padding: 12px; border-radius: 10px; box-shadow: 0 8px 30px rgba(3,18,38,0.12); }
.admin-tournament + .admin-tournament { margin-top: 12px; }

.admin-tournament .header { display:flex; justify-content:space-between; align-items:center; gap:12px; }
.admin-tournament .title { font-weight:800; color:#0b3b66; font-size:20px; }
.admin-tournament .meta { font-size:13px; color:#666; }

.admin-tournament table { width:100%; border-collapse: collapse; margin-top: 10px; }
.admin-tournament th, .admin-tournament td { text-align:left; padding: 10px 8px; border-bottom: 1px solid #f2f2f2; font-size:14px; }
.admin-tournament thead th { background: #fafafa; font-weight:800; }

.admin-tournament tbody tr:hover { background: #f7fbff; }

.admin-controls { display:flex; gap:8px; align-items:center; }
.admin-controls .btn { padding: 8px 12px; border-radius: 6px; font-weight:700; }
.admin-controls .btn.small { padding:6px 10px; }
.admin-controls .btn.back-btn { background:#ff6b1a; color:#fff; }
.admin-controls .btn.goto { background:#0b62a6; color:#fff; }

/* Larger "Open" button used in table action cells */
.admin-table .t-actions .btn.goto {
  background: #0b62a6;
  color: #fff;
  padding: 18px 28px;
  font-size: 28px;
  border-radius: 10px;
  font-weight: 900;
}
.admin-table .t-actions .btn.goto.small {
  padding: 14px 22px;
  font-size: 24px;
}

/* Make control-level goto button larger too */
.admin-controls .btn.goto {
  padding: 14px 20px;
  font-size: 20px;
  border-radius: 8px;
}

@media (max-width: 720px) {
  .admin-dashboard { padding: 16px; }
  .admin-dashboard h2 { font-size: 48px; }
  .admin-tournament .title { font-size: 18px; }
}
