:root{
  --yellow:#ffea00;
  --black:#000;
  --panel-bg: rgba(0,0,0,0.35);
}
.tournament-container{
  min-height:100vh;
  display:flex;
  align-items:center;
  justify-content:center;
  position:relative;
  overflow:hidden;
}

/* subtle dark overlay over the background image for contrast */
.tournament-container::before{
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(6,8,20,0.55);
  pointer-events: none;
}

.tournament-inner{
  position: relative;
  text-align:center;
  padding: clamp(20px, 4vw, 56px) clamp(18px, 4vw, 44px);
  border-radius:22px;
  width:800px;
  max-width:92%;
  background: linear-gradient(180deg, rgba(0,0,0,0.45), rgba(0,0,0,0.55));
  box-shadow: 0 40px 110px rgba(0,0,0,0.72), 0 10px 0 rgba(0,0,0,0.3);
  color: #fff;
  backdrop-filter: blur(6px) saturate(120%);
}

.tournament-title{
  background: var(--yellow);
  color: var(--black);
  display:inline-block;
  padding: clamp(8px, 2.5vw, 14px) 28px;
  font-weight:900;
  border-radius:14px;
  font-size: clamp(28px, 6vw, 70px);
  letter-spacing:1px;
  box-shadow: 0 8px 0 rgba(0,0,0,0.6);
}

.tournament-art{
  margin:18px 0 8px 0;
}

.tournament-art img{
  width:100%;
  max-width:420px;
  height:auto;
  border-radius:14px;
  box-shadow: 0 28px 64px rgba(0,0,0,0.7);
  border: 4px solid rgba(255,255,255,0.04);
}

/* Form inputs inside tournament pages */
.input-label{
  display:block;
  font-size:60px;
  color: rgba(255,255,255,0.98);
  margin-bottom:30px;
  font-weight:800;
}

.input-field{
  display:block;
  width:100%;
  padding:8px 10px;
  font-size:1.8rem;
  border-radius:16px;
  border: 4px solid rgba(0,0,0,0.6);
  background: rgba(255,255,255,0.06);
  color: #fff;
  outline: none;
  box-shadow: inset 0 2px 4px rgba(0,0,0,0.35);
}

.input-field:focus{
  border-color: rgba(255,235,0,0.95);
  background: rgba(255,255,255,0.02);
}

/* Placeholder sizing (cross-browser) */
.input-field::placeholder{ font-size:1.8rem; color: rgba(255,255,255,0.6); }
.input-field:-ms-input-placeholder{ font-size:1.8rem; color: rgba(255,255,255,0.6); }
.input-field::-ms-input-placeholder{ font-size:1.8rem; color: rgba(255,255,255,0.6); }
.input-field::-webkit-input-placeholder{ font-size:1.8rem; color: rgba(255,255,255,0.6); }
.input-field::-moz-placeholder{ font-size:1.8rem; color: rgba(255,255,255,0.6); }

.tournament-buttons{
  margin-top:40px;
}

.row{ display:flex; gap:12px; justify-content:center; margin-bottom:12px; }

.btn{
  background: linear-gradient(180deg, var(--yellow), #ffd300);
  border: 3px solid var(--black);
  padding:14px 20px;
  border-radius:20px;
  font-weight:900;
  cursor:pointer;
  min-width:160px;
  box-shadow: 0 10px 0 rgba(0,0,0,0.38);
  transition: transform 120ms ease, box-shadow 120ms ease;
}

/* keyboard focus accessibility */
.btn:focus-visible{
  outline: 3px solid rgba(255,235,0,0.95);
  outline-offset: 3px;
}

/* enlarge the top action buttons (View Upcoming / My Tournaments) */
.tournament-buttons .row:first-of-type .btn{
  min-width: 330px;
  padding: 18px 27px;
  font-size: 1.5rem;
  border-radius: 18px;
}

.btn:hover{ transform: translateY(-3px); box-shadow: 0 14px 20px rgba(0,0,0,0.4); }
.btn:active{ transform: translateY(1px); }

.create-btn{ min-width:260px; padding:16px 24px; border-radius:22px; }
.back-btn{ min-width:160px; }

/* Admin-specific styling for the Create Tournament button */
.admin-create{
  background: linear-gradient(180deg,#ffef66,#ffd200);
  color: #111;
  border: 3px solid rgba(0,0,0,0.7);
  box-shadow: 0 14px 28px rgba(255,210,40,0.12), 0 6px 0 rgba(0,0,0,0.35);
  transform: translateY(0);
}
.admin-create:hover{
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 20px 36px rgba(255,210,40,0.16), 0 8px 0 rgba(0,0,0,0.38);
}
.admin-create:focus-visible{
  outline: 0;
  box-shadow: 0 0 0 6px rgba(255,210,40,0.12), 0 14px 28px rgba(0,0,0,0.28);
}

/* responsive */
@media (max-width:920px){
  .tournament-inner{ width:92%; padding:28px; }
  .row{ flex-direction:column; gap:12px; }
  .create-btn{ min-width:100%; }
  .tournament-art img{ width:60%; }
  .tournament-title{ font-size:28px; padding:12px 18px; }
  /* ensure the top action buttons become full-width on small screens */
  .tournament-buttons .row:first-of-type .btn{ min-width:100%; }
}

/* Styles for Upcoming Tournaments view */
.upcoming-container{
  min-height: calc(100vh - 80px);
  padding: 36px 24px;
  display:flex;
  flex-direction:column;
  align-items:center;
  color: #fff;
}

/* Scale the upcoming view by 2.5x */
.upcoming-container{
  transform: scale(1.5);
  transform-origin: top center;
}
.upcoming-title{
  background: var(--yellow);
  color: var(--black);
  padding: 18px 28px;
  border-radius: 12px;
  font-weight:900;
  font-size: clamp(48px, 8vw, 85px);
  margin-bottom: 18px;
  box-shadow: 0 8px 0 rgba(0,0,0,0.4);
}
.upcoming-empty{ color: rgba(255,255,255,0.85); }
.upcoming-list{ width:100%; max-width:820px; display:flex; flex-direction:column; gap:12px; }
.upcoming-item{
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:14px 18px;
  border-radius:12px;
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  box-shadow: 0 6px 18px rgba(0,0,0,0.45);
}
.upcoming-name{ font-size:1.1rem; font-weight:800; }
.upcoming-id{ font-size:0.95rem; color: rgba(255,255,255,0.75); margin-left:12px; }
.upcoming-actions{ margin-left:auto; }
.upcoming-actions .btn{ min-width:140px; padding:10px 14px; }
.upcoming-footer{ margin-top:20px; display:flex; gap:12px; }

@media (max-width:720px){
  .upcoming-item{ flex-direction:column; align-items:flex-start; gap:8px; }
  .upcoming-actions .btn{ min-width:100%; }
  .upcoming-footer{ flex-direction:column; width:100%; align-items:center; }
}

/* Smaller rectangular Join-by-Code button */
.join-code-btn{
  min-width:140px;
  padding:8px 12px;
  border-radius:6px;
  font-size:0.95rem;
  background: linear-gradient(180deg, rgba(255,255,255,0.06), rgba(255,255,255,0.02));
  border: 2px solid rgba(0,0,0,0.35);
  color: #fff;
  box-shadow: 0 6px 12px rgba(0,0,0,0.25);
}

@media (max-width:720px){
  .join-code-btn{ min-width:100%; }
}

/* Disable scale on smaller screens so layout stays usable */
@media (max-width:1400px){
  .upcoming-container{ transform: scale(1); }
}
