.history-page {
  background-image: url("../assets/bg.jpg"); /* or your background file */
  background-size: cover;          /* ✅ fills entire screen */
  background-repeat: no-repeat;    /* ✅ prevents repetition */
  background-position: center;     /* ✅ centers image */
  background-attachment: fixed;    /* ✅ keeps background still on scroll */
  
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;

  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 40px;
}

.history-title {
  background-color: #ffeb3b;
  color: black;
  padding: 25px 75px;
  border-radius: 25px;
  font-size: 6rem;
  font-weight: bold;
  box-shadow: 0 0 15px rgba(255, 235, 59, 0.8);
}

.history-table {
  width: 100%;
  max-width: 1320px;
  margin-top: 80px;
  background: rgba(0, 0, 0, 0.65);
  border-radius: 25px;
  box-shadow: 0 0 25px rgba(255, 235, 59, 0.2);
  overflow: hidden;
}

.table-header,
.table-row {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1.5fr;
  padding: 28px 40px;
  align-items: center;
  text-align: center;
  font-size: 1.8rem;
}

.table-header {
  background-color: #ffeb3b;
  color: black;
  font-weight: 900;
  font-size: 2rem;
}

.table-row {
  border-bottom: 2.5px solid rgba(255, 255, 255, 0.2);
}

.table-row:hover {
  background-color: rgba(255, 235, 59, 0.15);
}

.win {
  color: #00e676;
  font-weight: bold;
}

.lose {
  color: #ff5252;
  font-weight: bold;
}

.no-data {
  text-align: center;
  padding: 50px;
  color: #ccc;
  font-size: 2.4rem;
  font-weight: 700;
}

.back-btn {
  background-color: #ffeb3b;
  border: none;
  color: black;
  padding: 8px 18px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 30px;
  transition: 0.3s;
}

.back-btn:hover {
  background-color: #ffe740;
  transform: scale(1.05);
}
